import Vue from "vue";
import VueRouter from "vue-router";
import HomeView from "../views/HomeView.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    redirect: "/login",
  },
  {
    path: "/login",
    name: "login",
    component: () => import("../views/LoGin.vue"),
  },
  {
    path: "/home",
    name: "home",
    component: HomeView,
    redirect: {
      name: "about",
    },
    //路由嵌套
    children: [
      {
        path: "/about",
        name: "about",
        // route level code-splitting
        // this generates a separate chunk (about.[hash].js) for this route
        // which is lazy-loaded when the route is visited.
        component: () =>
          import(/* webpackChunkName: "about" */ "../views/AboutView.vue"),
        redirect: {
          name: "one",
        },
        children: [
          {
            path: "/about/one",
            name: "one",
            component: () => import("../views/about/AboutOne.vue"),
          },
          {
            path: "/about/two",
            name: "two",
            component: () => import("../views/about/AboutTwo.vue"),
          },
          {
            path: "/about/three",
            name: "three",
            component: () => import("../views/about/AboutThree.vue"),
          },
        ],
      },
      {
        path: "/Enter",
        name: "enter",
        // route level code-splitting
        // this generates a separate chunk (about.[hash].js) for this route
        // which is lazy-loaded when the route is visited.
        component: () =>
          import(/* webpackChunkName: "about" */ "../views/EnterPrise.vue"),
        children: [
          {
            path: "/list-item-:id",
            name: "enterson",
            component: () => import("../views/enter/EnterSon.vue"),
          },
        ],
      },
      {
        path: "/system",
        name: "system",
        component: () => import("../views/CharacterSystem.vue"),
      },
      {
        path: "/callto",
        name: "callto",
        component: () => import("../views/CalltotransferView.vue"),
      },

      {
        path: "/number",
        name: "number",
        component: () => import("../views/NumbermanagementView.vue"),
      },
    ],
  },
];

const router = new VueRouter({
  routes,
});

export default router;
